<template>
  <div class="home">
    <eve-sidebar class="eve-sidebar" />

    <router-view v-if="this.ebook.epub.isOpen" class="home-container">
      <eve-viewer></eve-viewer>
    </router-view>

    <div
      class="home-container"
      v-if="this.$route.path === '/reader/' && !isElectron"
    >
      <div class="home-wrapper">
        <div class="home-title">EvE Reader</div>
        <div class="home-open">
          <div class="drop-zone">
            <div class="hover-text">DROP<br />HERE</div>
          </div>
          <div style="font-size: 6rem">OR</div>
          <div class="home-icon-open" @click="openFile">
            <svg viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" width="128" height="128" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M952.32 424.755a86.528 86.528 0 00-54.784-29.696V207.36a86.63 86.63 0 00-86.528-86.528H545.69a86.63 86.63 0 00-86.528 86.528v38.4H178.38a86.63 86.63 0 00-86.221 86.63v62.055a34.202 34.202 0 002.15 11.673 87.552 87.552 0 00-21.504 17.818 86.22 86.22 0 00-20.48 68.71l46.388 335.975a86.938 86.938 0 0085.606 74.547h647.475a86.016 86.016 0 0085.402-72.602l54.477-335.974a86.323 86.323 0 00-19.354-69.837zM178.38 317.44h316.314a35.533 35.533 0 0035.533-35.533V207.36a15.462 15.462 0 0115.463-15.462h265.318a15.565 15.565 0 0115.462 15.462v186.88H162.918V332.8a15.565 15.565 0 0115.463-15.36zm722.74 165.786L847.053 819.2a15.36 15.36 0 01-15.258 13.005H184.32a15.462 15.462 0 01-15.155-13.005L122.88 482.918a14.95 14.95 0 013.584-12.288 15.462 15.462 0 0111.674-5.324h748.544a15.565 15.565 0 0111.366 5.734 15.155 15.155 0 013.072 12.186z"/></svg>
            Open File
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Ebook from "@/ebook";
import EveSidebar from "@/components/EveSidebar";
import EveViewer from "@/components/EveViewer";

export default {
  name: "reader",

  components: {
    EveSidebar,
    EveViewer,
  },

  data() {
    return {
      isElectron: true,
    };
  },

  async created() {
    this.ebook = new Ebook();
    this.$store.dispatch("setEbook", this.ebook);
    this.$store.dispatch("setting/initSetting");

    if (this.$route.name === "view") {
      let book_id = this.$route.params.book_id;
      let api = process.env.VUE_APP_API;
      let url = `${api}/show/${book_id}/epub/file.epub`;
      this.ebook.openEpubFromUrl(url);
    }

    // electron environment, listen for file open from argv
    if (window.ipcRenderer) {
      window.ipcRenderer.on("IPC::FILE-OPEN", (event, data, fileName) => {
        this.ebook.openEpub(data).then(() => {
          this.ebook.fileName = fileName;
          this.redirectRouter();
        });
      });
    } else {
      this.isElectron = false;
    }

    this.$bus.on("bus-open-file", this.openFile);
  },

  beforeDestroy() {
    this.$bus.off("bus-open-file");
  },

  methods: {
    openFile() {
      if (window.ipcRenderer) {
        window.ipcRenderer.send("IPC::ASK-OPEN-FILE");
      } else {
        this.ebook.openFile().then(() => {
          this.redirectRouter();
        });
      }
    },

    openFileFromDrop(file) {
      this.ebook.openFileFromDrop(file).then(() => {
        this.redirectRouter();
      });
    },

    redirectRouter() {
      this.$router.push(`/reader/open/${Math.random()}`);
    },
  },

  mounted() {
    if (!window.File) {
      throw new Error("Eve Reader are not supported by this browser!");
    }

    // Drag and Drop Event
    // -----------------------------------------------------------
    // Bug: by default, Drop epub to window always open the file.
    // but, opened epub is "iframe", if drop to iframe area, event doesn't work.
    let dropzone = document.querySelector(".drop-zone");

    function onDragOver(e) {
      if (e.dataTransfer.types.indexOf("Files") == -1) return;
      e.stopPropagation();
      e.preventDefault();
      e.dataTransfer.dropEffect = "copy";
      dropzone.classList.add("drop-zone-hover");
    }

    function onDragEnter(e) {
      if (e.dataTransfer.types.indexOf("Files") == -1) return;
      dropzone.classList.add("drop-zone-hover");
    }

    function onDragleave(e) {
      if (e.dataTransfer.types.indexOf("Files") == -1) return;
      dropzone.classList.remove("drop-zone-hover");
    }

    function onDragDrop(e) {
      e.stopPropagation();
      e.preventDefault();
      dropzone.classList.remove("drop-zone-hover");

      let fileList = e.target.files || e.dataTransfer.files;

      if (fileList.length == 0) return;

      let file = fileList[0]; // always choose the first file
      this.openFileFromDrop(file);
    }

    window.addEventListener("dragover", onDragOver);
    window.addEventListener("dragenter", onDragEnter);
    window.addEventListener("dragend", onDragleave);
    window.addEventListener("dragleave", onDragleave);
    window.addEventListener("drop", onDragDrop.bind(this));
    // End Drag and Drop Event
    // -------------------------------------------------------------
  },
};
</script>

